body{background:#323446}.flex{display:flex}.main-content{flex-direction:column;align-items:center;justify-content:center}.main-content h1{font-size:28px;font-family:'Montserrat', sans-serif;margin:20px 0;color:#ffd24c}.game-screen-container{height:500px;width:1000px;border-radius:2px;box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 1px 5px 0 rgba(0,0,0,0.12),0 3px 1px -2px rgba(0,0,0,0.2)}#bg-canvas{position:absolute;z-index:0}#canvas{position:absolute;z-index:10}#menu{position:absolute}#menu img{position:absolute;z-index:20}#start-btn,#controls-menu-btn{position:absolute;z-index:21;padding:0;font-weight:bold;background:transparent;border:none;outline:none;color:#ffd24c;cursor:pointer}#start-btn{top:300px;left:400px;width:200px;font-size:60px}#controls-menu-btn{top:400px;left:415px;width:170px;font-size:28px}#start-btn:hover,#controls-menu-btn:hover{color:#c09e38}#controls-menu{position:absolute;z-index:25;height:500px;width:1000px;background:rgba(0,0,0,0.4)}.inner-menu{flex-direction:column;align-items:center;height:375px;width:750px;border-radius:2px;margin:50px auto;padding-bottom:36px;background:#4c4f6a}.inner-menu h2,h4{font-size:32px;font-family:'Montserrat', sans-serif;margin:20px 0 10px 0;color:#ffd24c}.inner-menu p{font-size:16px;font-family:'Montserrat', sans-serif;text-transform:lowercase;margin:7px 0;color:#eaf6f3}.inner-menu span{color:#e16666;text-transform:uppercase}#close-btn{font-size:28px;font-family:'Montserrat', 'sans-serif';padding:0;background:transparent;border:none;outline:none;color:#2a8ca1;cursor:pointer}#close-btn:hover{color:#1e6170}#restart-msg{position:absolute;z-index:25;height:500px;width:1000px}#restart-msg h2{font-size:40px;font-weight:bold;font-family:sans-serif;margin-top:180px;color:#ffd24c}#restart-msg h4{font-size:36px;font-weight:bold;font-family:sans-serif;margin-top:40px;color:#ffd24c}.nav-links{flex-direction:row;align-items:center;margin-top:20px}.nav-link{height:50px;width:50px}.not-first{margin-left:16px}.angellist-link{height:55px;width:55px;margin-left:4px}.show{display:flex;flex-direction:column;align-items:center;background:rgba(0,0,0,0.4)}.hide,.closed{display:none}
